@import 'variables';

.card {
  width: 200px;
  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  display: flex;
  flex-direction: column;

  &__imac {
    white-space: nowrap;
    width: 166px;
    font-size: 12px;
    color: $darkBlueColor;
    font-weight: 500;
    line-height: 18px;
  }

  &__photoMac {
    display: block;
    height: 134px;
    margin: 0 3px 40px;
  }

  &__product {
    font-size: 10px;
    color: #616070;
    padding-top: 4px;
    line-height: 14px;
  }

  &__stars {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 16px;
    margin-bottom: 24px;
  }

  &__reviews {
    font-size: 10px;
    color: #060b35;
    line-height: 14px;
    margin-left: 17px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 16px;

    &-txt {
      font-size: 12px;
      color: #616070;
      line-height: 18px;
    }
  }

  &__costs {
    font-size: 16px;
    font-weight: 700;
    color: $darkBlueColor;
    line-height: 18px;
  }

  &__button {
    border: 1px solid $blueColor;
    background-color: $blueColor;
    width: 100%;
    height: 40px;
    border-radius: 5px;
    color: $whiteColor;
    font-weight: 700;
    font-size: 14px;
    text-decoration: none;
    line-height: 16px;
    align-items: center;
    text-transform: uppercase;
    text-align: center;
    display: flex;
    justify-content: center;

    &:hover {
      background-color: $whiteColor;
      color: $blueColor;
    }
  }
}
